<!-- 角色管理 -->
<template>
	<el-container style="background:#fff;padding: 10px">
		<div class="left-content">
			<div class="type-bar">
				<span @click="clickAddNode" class="el-icon-plus"></span>
				<span @click="clickReviseNode" class="el-icon-edit"></span>
				<span @click="clickDeleteNode" class="el-icon-close"></span>
				<span @click="clickReloadNode" class="el-icon-refresh"></span>
				<div class="search-bar">
					<el-input placeholder="请输入角色" v-model="searchValueLeft" class="search-input" size="small">
						<i slot="suffix" class="el-input__icon search el-icon-search"></i>
					</el-input>
				</div>
			</div>
			<el-aside width="100%" style="flex:1;">
				<el-tree :expand-on-click-node="false" :data="tree" ref="leftTree"  highlight-current node-key="id" :props="defaultProps" @node-click="handleNodeClickFn"></el-tree>
			</el-aside>
		</div>
		<div class="right-content">
			<div class="current">
				<div class="cur-title">分配权限</div>
				<div class="search-bar">
					<el-input placeholder="请输入" v-model="searchValueRight" class="search-input" size="small">
						<i slot="suffix" class="el-input__icon search el-icon-search"></i>
					</el-input>
				</div>
				<span class="name">模块名称：</span>
			</div>
			<el-aside width="100%" style="flex:1;">
				<el-tree class="float-children border-blue" :expand-on-click-node="false" empty-text @check="changeChecked" :data="treeCenter" show-checkbox node-key="id" @node-click="handleNodeClickCenterFn" highlight-current default-expand-all ref="treeCenterDom" :props="defaultProps">
				</el-tree>
			</el-aside>
			<div class="submit-bar">
				<el-button class="btn" type="primary" size="small">返回</el-button>
				<el-button class="btn" type="primary" size="small">提交</el-button>
			</div>
		</div>
		<!-- 添加部门 -->
		<el-dialog :append-to-body="true" width="500px" :title="title" :visible.sync="dialogTableVisible">
			<el-aside style="max-height:300px;width:100%;">
				<el-form :model="listLi"  :rules="rules" ref="ruleForm" >
					<el-form-item label="上级部门" prop="node" :label-width="formLabelWidth">
						<el-input style="width:90%;" size="small" v-model="listLi.node" auto-complete="off"></el-input>
					</el-form-item>
					<el-form-item label="部门名称" prop="nodeName" :label-width="formLabelWidth">
						<el-input style="width:90%;" size="small" v-model="listLi.nodeName" auto-complete="off"></el-input>
					</el-form-item>
					<el-form-item label="部门代码" :label-width="formLabelWidth">
						<el-input style="width:90%;" size="small" v-model="listLi.nodeNum" auto-complete="off"></el-input>
					</el-form-item>
					<el-form-item label="性质" :label-width="formLabelWidth">
						<el-input style="width:90%;" size="small" v-model="listLi.nodeNature" auto-complete="off"></el-input>
					</el-form-item>
					<el-form-item label="属性" :label-width="formLabelWidth">
						<el-input style="width:90%;" size="small" v-model="listLi.nodeType" auto-complete="off"></el-input>
					</el-form-item>
					<el-form-item label="详细地址" :label-width="formLabelWidth">
						<el-input style="width:90%;" size="small" v-model="listLi.address" auto-complete="off"></el-input>
					</el-form-item>
					<el-form-item label="所属站点" :label-width="formLabelWidth">
						<el-input style="width:90%;" size="small" v-model="listLi.site" auto-complete="off"></el-input>
					</el-form-item>
				</el-form>
			</el-aside>	
			<div slot="footer" class="dialog-footer">
				<el-button size="small" @click="addCancel">取 消</el-button>
				<el-button size="small" type="primary" @click="addConfirm('ruleForm')">确 定</el-button>
			</div>
		</el-dialog>
	</el-container>
</template>
<script>
export default {
	name:'roleManage',
	data(){
		return {
			searchValueLeft: '',
			searchValueRight: '',
			dialogTableVisible: false,
			addIsRevise: null,
			title: '',
			tree: [],
			formLabelWidth: '82px',
			listLi: {
				node:'',
				nodeName: '',
				nodeNum: '',
				nodeNature: '',
				nodeType: '',
				address: '',
				site: ''
			},
			rules: {},
			defaultProps: {
				children: 'children',
				label: 'text'
			},
			treeCenter:[
			{
				id:'1',
				text: '深圳赤子物流有限公司',
				children: [{
					id:'2',
					text: '操作部',
					children: [{
						id:'7',
						text: '粤C24523'
					},
					{
						id:'100',
						text: '粤C24523'
					},
					{
						id:'101',
						text: '粤C24523'
					},
					{
						id:'102',
						text: '粤C24523'
					},
					{
						id:'103',
						text: '粤C24523'
					},
					{
						id:'104',
						text: '粤C24523'
					},
					{
						id:'105',
						text: '粤C24523'
					},
					{
						id:'106',
						text: '粤C24523'
					},
					{
						id:'107',
						text: '粤C24523'
					},
					{
						id:'9',
						text: '粤C24873'
					}]
				},
				{
					id:'3',
					text: '新车',
					children: [{
						id:'10',
						text: '粤C24523'
					},
					{
						id:'11',
						text: '粤C24873'
					}]
				}]
			}
			]
		}	
	},
	created(){
		var treeListLeft = [{
			id:'1',
			text: '深圳赤子物流有限公司',
			children: [{
				id:'2',
				text: '操作部'
			},
			{
				id:'3',
				text: '新车'
			}]
		}];
		this.tree = treeListLeft;
	},
	mounted() {
		
	},
	methods: {
		clickAddNode(){
			this.dialogTableVisible = true;
			this.title = "添加部门";
			this.addIsRevise = '1';
			for(var key in this.listLi){
				this.listLi[key] = '';
			}
		},
		clickReviseNode(){

		},
		clickDeleteNode(){

		},
		clickReloadNode(){

		},
		handleNodeClickFn(){

		},
		//右边
		changeChecked(){

		},
		handleNodeClickCenterFn(){

		},
		//取消按钮
		addCancel(){
			this.dialogTableVisible = false;
		},
		//确认按钮
		addConfirm(formName){
			this.$refs[formName].validate((valid) => {
				if (valid) {
					if(this.addIsRevise == '1') {
						console.log('添加部门')
					} else {
						console.log('修改部门')
					}
				} else {

					return false;
				}
			});
		},
		//点击删除部门
		clickDeleteNode() {
			if(this.carId != '') {
				this.$confirm('此操作将永久删除该部门, 是否继续?', '提示', {
					confirmButtonText: '确定',
					cancelButtonText: '取消',
					type: 'warning',
					center: true
				}).then(() => {
					this.$refs.leftTree.remove(this.carId);
					this.$message({
						type: 'success',
						message: '删除成功!'
					});
				}).catch(() => {
					this.$message({
						type: 'info',
						message: '已取消删除'
					});
				});
			} else {
				this.$message({
					type: 'warning',
					message: '请选择要删除的部门！'
				});
			}
		},
		//点击刷新部门
		clickReloadNode() {
			
		}
	}
}
</script>
<style scoped>
.left-content{border: 1px solid #ddd;display:flex;flex-direction:column;height:100%;width: 400px;margin-right: 20px;}
.move-info{display: flex;justify-content: center;align-items: center;padding: 40px 0 0;}
.move-info span{display: block;width: 40px;height: 28px;line-height: 28px;color: #fff;text-align: center;background: #00a8d4;font-size: 16px;margin: 10px auto;border-radius: 2px;cursor: pointer;}
.type-bar{overflow: hidden;height: 40px;border-bottom: 1px solid #ddd;}
.type-bar span{display: block;float: left;width: 32px;height: 32px;font-size: 20px;color: #00a8d4;line-height: 32px;text-align: center;cursor: pointer;margin: 4px 0 0 5px;box-sizing: border-box;border: 1px solid #fff;}
.type-bar span:hover{border: 1px solid #dedede;border-radius: 2px;}
.type-bar .search-bar{float: right;position: relative;top: 4px;right: 5px;}
.type-bar .search-bar .search-input{width: 160px;}
.type-bar .search-bar .search{cursor: pointer;font-size: 16px;}
.car-num-list{padding: 6px 20px 0;}
.type-bar .left{float: left;margin: 11px 0 0 10px;line-height: 19px;font-size: 14px;color: #00a8d4;}
.right-content{flex: 1;border: 1px solid #ddd;display:flex;flex-direction:column;height:100%;}
.right-content .current{overflow: hidden;height: 40px;line-height: 40px;border-bottom: 1px solid #ddd;padding: 0 10px;}
.right-content .cur-title{float: left;}
.right-content .search-bar,.right-content .name{float: right;}
.right-content .search-bar{width: 160px;}
.submit-bar{padding: 10px;overflow: hidden;}
.submit-bar .btn{float: right;margin-left: 10px;}
</style>


